.App-logo {
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.App-logo img{
  width: 100%;
  height: auto;
  min-height: 150px;
}

.App-text {
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  width: 100%;
}

h1 {
  margin: 0 10px;
  font-size: 3em;
  line-height: 1.25;
  font-family: EuropaBrush-Regular, arial, sans-serif;
  font-style: normal;
  font-weight: 400;
}

@media (min-width: 43.75em) {
  h1 {
    font-size: 4em;
    line-height: 1.125;
  }
}

@media (min-width: 56.25em) {
  h1 {
    font-size: 7em;
    line-height: 1.05;
  }
}

main {
  margin: 10px auto;
  font-size: 16px;
  
}
.ImageList {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 10px;
  grid-auto-flow: column;
  grid-auto-columns: calc(95% - 40px);
  overflow-x: scroll;
}

@media all and (min-width: 768px) {
  .ImageList {
    grid-auto-columns: calc(75% - 40px);
  }
}

.ImageList::-webkit-scrollbar {
  display: none;
}

.ImageList::before,
.ImageList::after {
  content: '';
  width: 10px;
}
.Image-container {
  margin: 15px;
	padding: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,.3);
  -webkit-transform: none;
          transform: none;
}

.Image-container::before {
  content: url(/wiegehtsdireuropa/static/media/clothespin.1923b01b.svg);
  margin-left: calc(50% - 15px);
  z-index: 2;
}

/* Rotate all even images -2 degrees */
.Image-container:nth-child(2n) {
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
}

/* Rotate every fifth image by 2 degrees */
.Image-container:nth-child(3n) {
  -webkit-transform: rotate(2deg);
          transform: rotate(2deg);
}

.duotone {
  position: relative;
  top: -10px;
}

.duotone::after {
  position: absolute;
  content: "\A9   Staatsarchiv Freiburg";
  content: "";
  font-size: 22px;
  top: 0;
  left: 0;
  bottom: 10px;
  right: 0;
  pointer-events: none;
}

.yellow-red {
  background-color: rgba(221, 204, 0, 1);
}

.yellow-red img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: darken;
}

.yellow-red::after {
  background-color: rgba(221, 0, 0, 1);
  mix-blend-mode: lighten;
  color: white;
}

.green-blue {
  background-color: #00ff36
}

.green-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.green-blue::after {
  background-color: #23278a;
  mix-blend-mode: lighten;
  color: white;
}

.purple-green {
  background-color: #88169d;
}

.purple-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: lighten;
}

.purple-green::after {
  mix-blend-mode: hard-light;
  background: -webkit-gradient(linear, right bottom, left top, from(#75d775), to(#321a5b));
  background: -webkit-linear-gradient(bottom right, #75d775, #321a5b);
  background: linear-gradient(to top left, #75d775, #321a5b);
}

.Image {
  width: 100%;
}
.BoxAsset {
  box-shadow: 0 4px 6px rgba(32, 33, 36, 0.28);
  font-size: 1.5em;
  font-weight: 400;
  margin: 10px;
  padding: 7px 10px 5px 10px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #19cf86;
  background-color: #FFF;
}
.Boxes {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  background-image: -webkit-linear-gradient(315deg, #8ac3cc 33.33%, #F6F0CF 33.33%, #F6F0CF 50%, #8ac3cc 50%, #8ac3cc 83.33%, #F6F0CF 83.33%, #F6F0CF 100%);
  background-image: linear-gradient(135deg, #8ac3cc 33.33%, #F6F0CF 33.33%, #F6F0CF 50%, #8ac3cc 50%, #8ac3cc 83.33%, #F6F0CF 83.33%, #F6F0CF 100%);
  background-size: 84.85px 84.85px;
}

.Droppable {
  width: 100%;
  min-height: 120px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
